@import-normalize;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    background: var(--app-default);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *:before, *:after {
  box-sizing: border-box;
  transition: .4 ease-in;
}


:root {
	--alpha-app-default: 55, 55, 57;
	--app-yellow: #FFCB05;
	--app-default: #373739;
	--app-landing: #F1EBEB;
	--rock: #B69E31;
	--ghost: #70559b;
	--steel: #b7b9d0;
	--water: #6493eb;
	--grass: #74cb48;
	--psychic: #fb5584;
	--ice: #9ad6df;
	--dark: #75574c;
	--fairy: #e69eac;
	--normal: #aaa67f;
	--fighting: #c12239;
	--flying: #a891ec;
	--poison: #a43e9e;
	--bug: #a7b723;
	--fire: #f57d31;
	--electric: #f9cf30;
	--dragon: #7037ff;
	--ground: #DEC16B;
}